<!--
 * @Author: Marlon
 * @Date: 2020-02-18 10:25:27
 * @LastEditors: Marlon
 * @LastEditTime: 2020-02-24 19:13:38
 * @Description: 库管理
 -->
<template>
  <div class="library-manage">
    <div class="library-manage-edit-area">
      <ul class="library-manage-edit-area-btn">
        <li>
          <el-button type="primary" size="medium" @click="showDialog({type:'新增库'})">新增库</el-button>
        </li>
        <li>
          <el-tooltip class="item" effect="dark" content="比较两个或多个项目之间的元数据的差异" placement="top">
            <el-button type="primary" size="medium">比较</el-button>
          </el-tooltip>
        </li>
      </ul>
      <ul class="library-manage-edit-area-select">
        <li>
          <label for>项目：</label>
          <el-select
            v-model="selectVal"
            filterable
            placeholder="请选择"
            size="small"
            @change="selectChange"
          >
            <el-option
              v-for="item in selectOpt"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </li>
      </ul>
    </div>
    <div class="library-manage-table">
      <TableDefault
        :TTitle="tableTitle"
        :TData="tableData"
        :TIsShowCheckBox="true"
        :TIsShowPage="true"
        :TEdit="TEdit"
        :TClickItem="TClickItem"
        @editBtnCb="editBtnCb"
      ></TableDefault>
    </div>
    <!-- 弹出框 -->
    <FormDialog
      :FDVisible="FDVisible"
      :FDTitle="FDTitle"
      FDWidth="500"
      :FDForm="FDForm"
      @editBtnCb="editBtnCbDiglog"
    ></FormDialog>
  </div>
</template>

<script>
import mixin from "mixin";

export default {
  mixins: [mixin],
  data() {
    return {
      // 编辑-表格配置
      TEdit: {
        show: true,
        title: "操作",
        editTypes: [
          {
            label: "编辑"
          },
          {
            label: "删除"
          },
          {
            label: "导入数据库结构"
          },
          {
            label: "导入数据标准"
          }
        ]
      },
      tableTitle: [
        {
          prop: "name",
          label: "所属项目"
        },
        {
          prop: "date",
          label: "数据表个数"
        },
        {
          prop: "address",
          label: "字段个数"
        }
      ],
      tableData: [
        {
          date: "122",
          name: "通用",
          address: "45"
        },
        {
          date: "122",
          name: "海淀",
          address: "45"
        },
        {
          date: "122",
          name: "山西",
          address: "45"
        },
        {
          date: "122",
          name: "济南",
          address: "45"
        },
        {
          date: "122",
          name: "山东",
          address: "45"
        },
        {
          date: "122",
          name: "贵州",
          address: "45"
        },
        {
          date: "122",
          name: "福州",
          address: "45"
        },
        {
          date: "122",
          name: "泰安",
          address: "45"
        },
        {
          date: "122",
          name: "南京",
          address: "45"
        },
        {
          date: "122",
          name: "广州",
          address: "45"
        }
      ],
      // 是否显示弹出框
      FDVisible: false,
      FDTitle: "",
      // 弹出框配置
      FDForm: [
        {
          type: "select",
          label: {
            name: "所属项目",
            width: "120"
          },
          width: "200",
          defaultModel: "",
          placeholder: "",
          option: [
            {
              label: "海淀",
              value: "haidian"
            }
          ]
        },
        {
          type: "input",
          label: {
            name: "中文名称",
            width: "120"
          },
          width: "280",
          defaultModel: "",
          placeholder: ""
        },
        {
          type: "input",
          label: {
            name: "库名",
            width: "120"
          },
          width: "280",
          defaultModel: "",
          placeholder: ""
        },
        {
          type: "textarea",
          label: {
            name: "备注",
            width: "120"
          },
          width: "280",
          defaultModel: "",
          placeholder: "",
          autosize: {
            min: 2,
            max: 4
          }
        }
      ],
      // 可跳转的值
      TClickItem: [
        {
          link: "/",
          cells: ["address"]
        }
      ],
      // 下拉选择
      selectOpt: [
        {
          value: "选项1",
          label: "所有项目"
        },
        {
          value: "选项2",
          label: "通用"
        },
        {
          value: "选项3",
          label: "南京"
        },
        {
          value: "选项4",
          label: "山东"
        },
        {
          value: "选项5",
          label: "济南"
        }
      ],
      selectVal: "选项1"
    };
  },
  methods: {
    // 表格-回调
    editBtnCb(editItem) {
      console.log(editItem);
      let text = editItem.label;
      switch (text) {
        case "删除":
          // TODO 发请求
          this.$message({
            type: "success",
            message: "删除成功!"
          });
          return;
        case "编辑":
          this.showDialog({ type: text });
          return;
        case "导入数据库结构":
          this.showDialog({ type: text });
          return;
        case "导入数据标准":
          this.showDialog({ type: text });
          return;
        default:
          break;
      }
    },
    // 弹出框-回调
    editBtnCbDiglog(obj, formReq) {
      this.FDVisible = false;
      if (obj === "确定") {
        // console.log(this.FDTitle);
        switch (this.FDTitle) {
          case "新增库":
            // TODO 发请求
            console.log(formReq);
            return;

          default:
            return;
        }
      }
    },
    // 显示弹出框并修改配置
    showDialog({ type }) {
      switch (type) {
        case "新增库":
          this.FDTitle = "新增库";
          this.FDVisible = true;
          this.FDForm = [
            {
              type: "select",
              label: {
                name: "所属项目",
                width: "120"
              },
              width: "200",
              defaultModel: "",
              placeholder: "",
              option: [
                {
                  label: "海淀",
                  value: "haidian"
                }
              ]
            },
            {
              type: "input",
              label: {
                name: "中文名称",
                width: "120"
              },
              width: "280",
              defaultModel: "",
              placeholder: ""
            },
            {
              type: "input",
              label: {
                name: "库名",
                width: "120"
              },
              width: "280",
              defaultModel: "",
              placeholder: ""
            },
            {
              type: "textarea",
              label: {
                name: "备注",
                width: "120"
              },
              width: "280",
              defaultModel: "",
              placeholder: "",
              autosize: {
                min: 2,
                max: 4
              }
            }
          ];
          return;
        case "编辑":
          this.FDTitle = "编辑库信息";
          this.FDVisible = true;
          this.FDForm = [
            {
              type: "select",
              label: {
                name: "所属项目",
                width: "120"
              },
              width: "200",
              defaultModel: "",
              placeholder: "",
              option: [
                {
                  label: "海淀",
                  value: "haidian"
                }
              ]
            },
            {
              type: "input",
              label: {
                name: "中文名称",
                width: "120"
              },
              width: "280",
              defaultModel: "",
              placeholder: ""
            },
            {
              type: "input",
              label: {
                name: "库名",
                width: "120"
              },
              width: "280",
              defaultModel: "",
              placeholder: ""
            },
            {
              type: "textarea",
              label: {
                name: "备注",
                width: "120"
              },
              width: "280",
              defaultModel: "",
              placeholder: "",
              autosize: {
                min: 2,
                max: 4
              }
            }
          ];
          return;
        case "导入数据库结构":
          this.FDTitle = "选择数据源";
          this.FDVisible = true;
          this.FDForm = [
            {
              type: "select",
              label: {
                name: "数据源",
                width: "120"
              },
              width: "200",
              defaultModel: "",
              placeholder: "",
              option: [
                {
                  label: "jydsj_dw",
                  value: "haidian"
                }
              ]
            }
          ];
          return;
        case "导入数据标准":
          this.FDTitle = "导入数据标准";
          this.FDVisible = true;
          this.FDForm = [
            {
              type: "select",
              label: {
                name: "选择数据标准",
                width: "120"
              },
              width: "200",
              defaultModel: "",
              placeholder: "",
              option: [
                {
                  label: "南京数据标准2.0",
                  value: "haidian"
                }
              ]
            }
          ];
          return;
        default:
          return;
      }
    },
    // 选择下拉
    selectChange(item) {
      // TODO 发请求
      console.log(item);
    }
  }
};
</script>

<style lang="less" scoped>
.library-manage {
  &-edit-area {
    overflow: hidden;
    &-btn {
      float: left;
      overflow: hidden;
      li {
        float: left;
        margin: 2px 15px;
      }
    }
    &-select {
      float: left;
      overflow: hidden;
      margin: 4px 55px;
    }
  }
  &-table {
    margin: 20px 0;
  }
}
</style>



